<template>
  <el-container class="chat-page">
    <el-aside width="260px" class="chat-list-box">
      <div class="chat-list-header">
        <el-input class="search-text" size="small" placeholder="ÊêúÁ¥¢" v-model="searchText">
          <i class="el-icon-search el-input__icon" slot="prefix"> </i>
        </el-input>
      </div>
      <div class="chat-list-loading" v-if="loading" v-loading="true" element-loading-text="Ê∂àÊÅØÊé•Êî∂‰∏≠..."
        element-loading-spinner="el-icon-loading" element-loading-background="#F9F9F9" element-loading-size="24">
      </div>
      <el-scrollbar class="chat-list-items" v-else>
        <div v-for="(chat, index) in sortedChats" :key="generateChatKey(chat, index)">
          <chat-item v-show="!chat.delete && chat.showName && chat.showName.includes(searchText)" :chat="chat" :index="chat.originalIndex"
            @click.native="onActiveItem(chat.originalIndex)" @delete="onDelItem(chat.originalIndex)" @top="onTop(chat.originalIndex)"
            :active="chat === chatStore.activeChat" :class="{
              'terminal-chat': chat.isTerminalChat || chat.isTerminalInbox,
              'normal-chat': !chat.isTerminalChat && !chat.isTerminalInbox
            }"></chat-item>
        </div>
      </el-scrollbar>
    </el-aside>
    <el-container class="chat-box">
      <!-- ÂÆπÂô®ÁÆ°ÁêÜÁªÑ‰ª∂ - ‰∫ëÁîµËÑëËøúÁ®ãÂ∑•‰ΩúÂè∞ -->
      <container-manager v-if="isPCTerminalChat" ref="containerManager"></container-manager>
      
      <chat-box v-if="chatStore.activeChat" :chat="chatStore.activeChat" @message-sent="onMessageSent"></chat-box>
    </el-container>
  </el-container>
</template>

<script>
import ChatItem from "../components/chat/ChatItem.vue";
import ChatBox from "../components/chat/ChatBox.vue";
import ContainerManager from "../components/chat/ContainerManager.vue";

export default {
  name: "chat",
  components: {
    ChatItem,
    ChatBox,
    ContainerManager
  },
  data() {
    return {
      searchText: "",
      messageContent: "",
      group: {},
      // ÁªàÁ´ØÈó¥ÈÄö‰ø°Áõ∏ÂÖ≥
      showTerminalSelector: false,
      // PCÁªàÁ´Ø‰ºöËØùÊ£ÄÊµã
      isPCTerminalChat: false,
      groupMembers: []
    }
  },
  methods: {
    onActiveItem(index) {
      this.$store.commit("activeChat", index);
      this.detectPCTerminalChat();
    },
    
    // Ê£ÄÊµãÊòØÂê¶‰∏∫PCÁªàÁ´Ø‰ºöËØù
    detectPCTerminalChat() {
      console.log('[Chat] detectPCTerminalChat ÂºÄÂßãÊ£ÄÊµã');
      console.log('[Chat] activeChat:', this.chatStore.activeChat);
      
      if (!this.chatStore.activeChat) {
        console.log('[Chat] Ê≤°ÊúâÊ¥ªË∑É‰ºöËØùÔºåisPCTerminalChat = false');
        this.isPCTerminalChat = false;
        return;
      }
      
      const targetId = this.chatStore.activeChat.targetId;
      console.log('[Chat] ‰ºöËØù‰ø°ÊÅØ:', {
        targetId: targetId,
        showName: this.chatStore.activeChat.showName
      });
      
      // ÁÆÄÂåñÂà§Êñ≠ÔºöÂè™Ë¶ÅtargetIdÂåÖÂê´"_3"Â∞±ÊòØPCÁªàÁ´Ø‰ºöËØù
      // Â∞ÜtargetIdËΩ¨Êç¢‰∏∫Â≠óÁ¨¶‰∏≤ÂêéÂÜçË∞ÉÁî®includesÊñπÊ≥ï
      if (targetId && String(targetId).includes('_3')) {
        this.isPCTerminalChat = true;
        console.log('[Chat] Ê£ÄÊµãÂà∞PCÁªàÁ´Ø‰ºöËØù (targetIdÂåÖÂê´_3)ÔºåÊòæÁ§∫ÂÆπÂô®ÁÆ°ÁêÜ');
      } else {
        this.isPCTerminalChat = false;
        console.log('[Chat] ÈùûPCÁªàÁ´Ø‰ºöËØùÔºå‰∏çÊòæÁ§∫ÂÆπÂô®ÁÆ°ÁêÜ');
      }
      
      console.log('[Chat] ÊúÄÁªà isPCTerminalChat =', this.isPCTerminalChat);
    },
    onDelItem(index) {
      this.$store.commit("removeChat", index);
    },
    onTop(chatIdx) {
      this.$store.commit("moveTop", chatIdx);
    },
    
    // Ëé∑ÂèñÂΩìÂâçÁªàÁ´ØÁ±ªÂûã
    getCurrentTerminal() {
      // Ê†πÊçÆÁéØÂ¢ÉÂà§Êñ≠ÂΩìÂâçÁªàÁ´ØÁ±ªÂûã
      if (typeof window !== 'undefined') {
        // Ê£ÄÊµãVSCodeÁéØÂ¢É
        if (window.parent !== window || 
          window.acquireVsCodeApi || 
          window.isVSCodeEnvironment || 
          (window.location && window.location.protocol === 'vscode-webview:')) {
          return 2; // VSCode
        }
        // Ê£ÄÊµãPCÂÆ¢Êà∑Á´ØÁéØÂ¢É
        if (window.isElectronApp || window.electronAPI) {
          return 3; // PC
        }
        // Ê£ÄÊµãÊèí‰ª∂ÁéØÂ¢É
        if (window.chrome && window.chrome.runtime && window.chrome.runtime.id) {
          return 4; // Êèí‰ª∂
        }
        return 0; // Web
      }
      // ÁßªÂä®Á´ØÁéØÂ¢É
      if (typeof uni !== 'undefined') {
        return 1; // App
      }
      return 0; // ÈªòËÆ§Web
    },
    
    // ÁîüÊàê‰ºöËØùÊòæÁ§∫KeyÔºàÁî®‰∫éVueÁöÑkeyÂ±ûÊÄßÔºâ
    generateChatKey(chat, index) {
      if (chat.isTerminalChat) {
        return `terminal-chat-${chat.senderTerminal}-${chat.targetId}`;
      } else if (chat.isTerminalInbox) {
        return `terminal-inbox-${chat.receivingTerminal}-${chat.targetId}`;
      } else {
        return `${chat.type}-${chat.targetId}-${index}`;
      }
    },
    
    // Ê∂àÊÅØÂèëÈÄÅÂ§ÑÁêÜ
    onMessageSent(message) {
      console.log('Ê∂àÊÅØÂ∑≤ÂèëÈÄÅ:', message);
      
      // Â¶ÇÊûúÂÆπÂô®ÁÆ°ÁêÜÈù¢ÊùøÂ≠òÂú®‰∏îÊú™ÊäòÂè†ÔºåÂàôËá™Âä®ÊäòÂè†
      if (this.$refs.containerManager && !this.$refs.containerManager.isCollapsed) {
        console.log('[Chat] ÂèëÈÄÅÊ∂àÊÅØÔºåËá™Âä®ÊäòÂè†ÂÆπÂô®ÁÆ°ÁêÜÈù¢Êùø');
        this.$refs.containerManager.toggleCollapse();
      }
    }

  },
  computed: {
    chatStore() {
      return this.$store.state.chatStore;
    },
    loading() {
      return this.chatStore.loadingGroupMsg || this.chatStore.loadingPrivateMsg
    },
    // ÊéíÂ∫èÂêéÁöÑ‰ºöËØùÂàóË°®ÔºàÁªàÁ´Ø‰ºöËØùÁΩÆÈ°∂Ôºâ
    sortedChats() {
      const chats = this.chatStore.chats || [];
      
      // Ê∑ªÂä†ÂéüÂßãÁ¥¢ÂºïÔºåÁî®‰∫é‰øùÊåÅÊìç‰ΩúÁöÑÊ≠£Á°ÆÊò†Â∞Ñ
      const chatsWithIndex = chats.map((chat, index) => ({
        ...chat,
        originalIndex: index
      }));
      
      // ÂàÜÁ¶ªÁªàÁ´Ø‰ºöËØùÂíåÊôÆÈÄö‰ºöËØù
      const terminalChats = [];
      const normalChats = [];
      
      chatsWithIndex.forEach(chat => {
        if (chat.isTerminalChat || chat.isTerminalInbox) {
          terminalChats.push(chat);
        } else {
          normalChats.push(chat);
        }
      });
      
      // ÁªàÁ´Ø‰ºöËØùÊåâÁªàÁ´ØÁ±ªÂûãÊéíÂ∫è
      terminalChats.sort((a, b) => {
        const aTerminal = a.senderTerminal || a.receivingTerminal || 0;
        const bTerminal = b.senderTerminal || b.receivingTerminal || 0;
        return aTerminal - bTerminal;
      });
      
      // ÊôÆÈÄö‰ºöËØùÊåâÊúÄÂêéÂèëÈÄÅÊó∂Èó¥ÊéíÂ∫è
      normalChats.sort((a, b) => {
        const aTime = a.lastSendTime || 0;
        const bTime = b.lastSendTime || 0;
        return bTime - aTime;
      });
      
      // ËøîÂõûÂêàÂπ∂ÂêéÁöÑÂàóË°®ÔºöÁªàÁ´Ø‰ºöËØùÂú®ÂâçÔºåÊôÆÈÄö‰ºöËØùÂú®Âêé
      return [...terminalChats, ...normalChats];
    }
  },
  mounted() {
    // ÂàùÂßãÂåñÊó∂Ê£ÄÊµãÂΩìÂâçÊ¥ªË∑É‰ºöËØù
    this.detectPCTerminalChat();
  },
  watch: {
    'chatStore.activeChat': {
      handler() {
        // ÂΩìÊ¥ªË∑É‰ºöËØùÊîπÂèòÊó∂ÔºåÈáçÊñ∞Ê£ÄÊµã
        this.detectPCTerminalChat();
      },
      immediate: true
    }
  }
}
</script>

