        // Ê∑ªÂä†ÁªàÁ´ØÊ†áËØÜÂõæÊ†á
        &::before {
          content: "üñ•Ô∏è";
          position: absolute;
          right: 10px;
          top: 50%;
          transform: translateY(-50%);
          font-size: 16px;
          opacity: 0.7;
        }
      }
      
      // ÊôÆÈÄö‰ºöËØùÊ†∑Âºè
      ::v-deep .normal-chat {
        background: #fff;
        
        &:hover {
          background: #f5f7fa;
        }
      }
    }
  }
}
